.wrapper {
  display: flex;
}
.columncontact {
  display: flex;
  flex-direction: column;
}
.columncontact > div {
  font-size: 4vh;
  color: #09d3ac;
  background: white;
  margin: .1em;
  padding: .3em 1em;
  border-radius: 8px;
  box-shadow: 0px 2px 24px 0px rgba(0, 0, 0, 0.15);
  flex: 1;
}

* {
  box-sizing: border-box;
}
// .App {
//    max-width: 420px;
//    margin: 0 auto;
// }

textarea {
  width: 300px;
  height: 150px;
}
input[type=text],input[type=email], select, textarea {
   width: 100%;
   padding: 12px;
   border: 1px solid #ccc;
   border-radius: 8px;
   box-sizing: border-box;
   margin-top: 6px;
   margin-bottom: 16px;
   resize: vertical;
}
input[type=submit] {
   background-color: #09d3ac;
   color: white;
   padding: 12px 20px;
   border: none;
   border-radius: 8px;
   cursor: pointer;
}
input[type=submit]:hover {
   background-color: #067c65;
}
.container {
   border-radius: 8px;
   padding: 20px;
}