#wrapper {
  /* display: flex; */
  width: 220px;
}

#signature {
	max-width: 350px;
  }
  path {
	fill: none;
	stroke: #000;
	stroke-width: 2;
	stroke-linecap: round;
	stroke-linejoin: round;
	stroke-miterlimit: 10;
  }
  .line1 {
	stroke-dasharray: 130.75833129882812;
  -webkit-animation: write1 2s linear;
  -moz-animation: write1 2s linear;
			animation: write1 2s linear;
  }
  .line2 {
	stroke-dasharray: 264.064697265625;
  -webkit-animation: write2 3s linear;
  -moz-animation: write2 3s linear;
			animation: write2 3s linear;
  }
  .line3 {
	stroke-dasharray: 293.2086181640625;
  -webkit-animation: write3 4s linear;
  -moz-animation: write3 4s linear;
			animation: write3 4s linear;
  }
  .line4 {
	stroke-dasharray: 1056.37548828125;
  -webkit-animation: write4 5s linear;
  -moz-animation: write4 5s linear;
			animation: write4 5s linear;
  }
  
  @-webkit-keyframes write1 {
	0% {
	  stroke-dashoffset: 130.75833129882812;
	}
	15% {
	  stroke-dashoffset: 0;
	}
  }
  @keyframes write1 {
	0% {
	  stroke-dashoffset: 130.75833129882812;
	}
	15% {
	  stroke-dashoffset: 0;
	}
  }
  @-webkit-keyframes write2 {
	0%, 20% {
	  stroke-dashoffset: 264.064697265625;
	}
	35% {
	  stroke-dashoffset: 0;
	}
  }
  @keyframes write2 {
	0%, 20% {
	  stroke-dashoffset: 264.064697265625;
	}
	35% {
	  stroke-dashoffset: 0;
	}
  }
  @-webkit-keyframes write3 {
	0%, 38% {
	  stroke-dashoffset: 293.2086181640625;
	}
	60% {
	  stroke-dashoffset: 0;
	}
  }
  @keyframes write3 {
	0%, 38% {
	  stroke-dashoffset: 293.2086181640625;
	}
	60% {
	  stroke-dashoffset: 0;
	}
  }
  
  @-webkit-keyframes write4 {
	0%, 61% {
	  stroke-dashoffset: 1056.37548828125;
	}
	100% {
	  stroke-dashoffset: 0;
	}
  }
  
  @keyframes write4 {
	0%, 61% {
	  stroke-dashoffset: 1056.37548828125;
	}
	100% {
	  stroke-dashoffset: 0;
	}
  }

.menu {
  background-color: white;
  box-shadow: 0px 2px 24px 0px rgba(0, 0, 0, 0.15);
  border-radius: 8px;
  width: 80%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  padding: 0 40px;
  position: relative;
}

.menu__right {
  display: flex;
  align-items: center;
  height: 100%;
}

.menu__list {
  list-style-type: none;
  padding: 0;
  height: 100%;
  margin: 0;
  margin-right: 40px;
}

.menu__list-item {
  display: inline-block;
  height: 100%;
  margin-right: 20px;
}

.menu__list-item:last-child {
  margin-right: 0;
}

.menu__link {
  color: #3a3e47;
  display: inline-block;
  height: 100%;
  font-size: 16px;
  text-decoration: none;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  padding: 0 3px;
}

.menu__link:hover {
  color: #09d3ac;
}

.menu__link--active {
  color: #09d3ac;
  border-top: 4px solid #09d3ac;
  font-weight: 700;
}

.menu__logo {
  text-indent: -9999999px;
  display: inline-block;
  height: 100%;
  width: 92px;
  background-size: cover 100%;
  background-repeat: no-repeat;
  background-position: center;
  margin: 0;
}

.menu__search-button {
  height: 42px;
  width: 42px;
  border-radius: 50%;
  border: 1px solid #c8c9ce;
  background-color: transparent;
  outline: none;
  cursor: pointer;
  background-repeat: no-repeat;
  background-position: center;
}

.menu__search-form {
  position: absolute;
  top: 100%;
  right: 0;
  padding: 10px;
  background-color: white;
}

.menu__search-input {
  outline: none;
  border: 1px solid #c8c9ce;
  font-size: 18px;
  padding: 5px;
  font-weight: 300;
}

#root{
  width: 100%;
}
